，但求整體氣韻生動自然。這是一種帶有創造性的臨習方法，不適於初習者運用，等學習者有一定臨寫基本功後可試用。")])])],1),_c('h1',{staticClass:"mt-16 t5"},[_vm._v("臨帖常見的問題")]),_c('div',{staticClass:"row"},[_c('div',{staticClass:"mx-auto col-sm-12 col-md-8 col-lg-5 col-xl-5 col-12 text-center"},[_c('h3',[_vm._v("筆畫寫不到位")]),_c('h4',[_vm._v("塗描結合")]),_c('p',{staticClass:"mt-5"},[_vm._v("要教會學習者邊臨帖邊體會，第一個字哪裡寫得不像，哪個部位臨得不準，第二遍加以改進。在寫字的過程中。要不斷加以分析、改進，力求字如法帖。久而久之，就能養成良好的寫字習慣，就能一筆到位，不再塗描。")])])]),_c('div',{staticClass:"row"},[_c('div',{staticClass:"mx-auto col-sm-12 col-md-8 col-lg-5 col-xl-5 col-12 text-center"},[_c('h3',[_vm._v("動手不動腦")]),_c('h4',[_vm._v("似臨實抄")]),_c('p',{staticClass:"mt-5"},[_vm._v("抄帖並沒有把字帖上字的筆畫、結構學到手。要克服這個毛病，也並非難事。只要讓學習者先將字帖中要寫的字仔細地看一看，並記住筆畫和結構的主要特徵，做到“形在心中，意在筆先”，下筆臨寫時，才能心中有數，做到心手合一，達到理想的臨寫效果，也就是背臨。每寫一遍。都要與字帖對照，找出筆畫、結構好的方面和差距，下一遍臨寫時注意縮小差距。長此以往，進步會更大，抄帖的問題也會得到解決。")])])]),_c('div',{staticClass:"row"},[_c('div',{staticClass:"mx-auto col-sm-12 col-md-8 col-lg-5 col-xl-5 col-12 text-center"},[_c('h3',[_vm._v("看一筆寫一筆")]),_c('h4',[_vm._v("光臨不背")]),_c('p',{staticClass:"mt-5 mb-16"},[_vm._v("學習者多習楷書，楷書筆畫雖各自獨立，但筆畫之間，還是有聯繫的。比如，一筆寫完再寫下一筆時，筆在空中有個走動的線路，同時形成下一筆逆勢，這個逆勢就是下一筆的藏鋒動作，也叫借勢落筆，可省藏鋒動作，能使筆畫自然協調。因此對字帖中較熟悉的字，可先看兩筆或三筆，連續寫兩筆或三筆，直到過渡到一次就能寫出一個完整的字來。")])])])])])])}
